// React useState  
import { useState } from 'react';

// CSS  Bootstrap 
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

// React-Bootstrap   
import { Container, Nav, Navbar, Row, Col, Form } from 'react-bootstrap';

// src    
import goods4 from './img/cont3_cake4.png'

// jQuery    
import UseJquery from './UseJquery';

//    (data.js  )
import data from './data';

//    ( )
import { Routes, Route, Link } from 'react-router-dom';

//    2 
import Company from './routes/Company';
import Product from './routes/Product';


function App() {
  //   state  (  state )
  let [goods] = useState(data);

  return (
    <div className="App">

      {/*   ( ) */}
      <Navbar bg="dark" data-bs-theme="dark">
        <Container>
          <Navbar.Brand href="#home">
            {/*   */}
            <img src="https://jellyzoo.github.io/ex_img/logo.png" alt='logo' />
          </Navbar.Brand>
          <Nav className="me-auto">
            {/*   (a ) */}
            <Nav.Link href="#pascucci"></Nav.Link>
            <Nav.Link href="#menu"></Nav.Link>
            <Nav.Link href="#info"></Nav.Link>
            <Nav.Link href="#notice"></Nav.Link>
          </Nav>
        </Container>
      </Navbar>

      {/*    ( ) */}
      <Routes>
        <Route path="/" />
        <Route path="/Company" element={<Company />} />
        <Route path="/Product" element={<Product />} />
      </Routes>


      {/*     */}
      <div className='main-bg'></div>

      {/*     */}
      <Container>
        <Row>
          {/*    Card   */}
          {
            goods.map((a, i) => {
              return (
                <Card goods={goods[i]} i={i + 1}></Card>
              );
            })
          }
        </Row>
      </Container>
    </div>
  );
}


/* Card  (   ) */
function Card(props) {
  return (
    <Col>
      {/* public    (    ) */}
      <img src={process.env.PUBLIC_URL + "/img/cont3_cake" + props.i + ".png"}
        width="50%"
        alt='listImg1' />
      {/*     */}
      <h4>{props.goods.title}</h4>
      <p>{props.goods.price}</p>
    </Col>
  )
}



export default App;


