import { Link } from "react-router-dom";

const Product = () => {
    return (
        <div>
            <ul>
                <li>
                    <Link to="/Company" className="Company_a"></Link>
                </li>
                <li>
                    <Link to="/Product" className="Product_a"></Link>
                </li>
            </ul>
            <h1></h1>
            <p> .</p>
        </div>
    );
}

export default Product;